.Episode {
  display: block;
  width: 80%;
  border: 1px solid black;
  background-color: aliceblue;
  padding: 3px;
  margin: 2px auto;
  cursor: pointer;
  text-decoration: none;
  color: black;
  text-align: center;
}

.Episode:hover {
  background-color: antiquewhite;
  -webkit-animation: enlargen 700ms ease-out forwards;
          animation: enlargen 700ms ease-out forwards;
}

@-webkit-keyframes enlargen{
  
  20%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100%{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}

@keyframes enlargen{
  
  20%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100%{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}



